import React, { Component } from 'react'
import '../src/App.css'
class App extends Component {

  state = {
    loading: true,
    covidData: null,
  }

  async componentDidMount() {
    const url = 'https://corona.lmao.ninja/v2/countries/China?yesterday&strict&query%20/'
    const response = await fetch(url)
    const data = await response.json()
    console.log(data)
    this.setState({
      covidData: data, loading: false
    })
  }

  render() {
    return (
      <div className={`App`}>
        <div>{this.state.loading || !this.state.covidData ? (
          <div>loading...</div>
        ) : (
          <div>
            <div><img src={this.state.covidData.countryInfo.flag} alt='' /></div>
            <div><h1>Covid-19 Deaths</h1><br/>
            <h2>Today: {this.state.covidData.todayDeaths}</h2>
            <h2>Total: {this.state.covidData.deaths}</h2></div>
            <h1></h1>
          </div>
        )}</div>
      </div>
    )
  }
}

export default App
